.add-todo-sec{
    width: 100%;
    text-align: right;
    margin-top: 50px;
}

.newInput{
    width: 80%;
    height: 35px;
    margin-right: 10px;
    padding-left: 10px;
}

.newBtn{
    height: 35px;
    width: fit-content;
    background-color: #4caf50;
    border: none;
    border-radius: 30px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bolder;
    cursor: pointer;
}

.newBtn:hover{
    background-color: #60d663;    
}

