.todo{
  border-bottom: dotted 1px black;
  height: fit-content;
  cursor: pointer;
  transition: background-color 300ms ease;
  display: flex;
  align-items: baseline;
}

.todo:hover{
  background-color: rgb(211, 211, 211);
}

.todo .option{
  display: none;
  margin: 0 0 0 auto;
}

.todo:hover .option{
  display: block;
}

.todo .option{
  align-self: center;
  margin-right: 10px;
}

.todo .option:hover{
  filter: drop-shadow(0em 0em .3em red);
}


.todo p{
  width: 94%;
  margin-bottom: 0;
  overflow-x: hidden;
}

.checked p{
  color: green;
  text-decoration: line-through;
}